#skillsChart {
  position: absolute;
  width: calc((80vw + 80vh)/2) ;
  height: calc((80vw + 80vh)/2);
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;

  #skillsChart-svg {
    width: 100%;
    height: 100%;
    opacity: 0.3;
    path {
      transition: fill 0.3s;
    }
  }
}


